import React from "react";
import Base from "../Base";
import { useParams } from "react-router-dom";
import Skills from "../components/SkillsView/Skills";

const SkillsView = () => {
  const { id } = useParams();
  return (
    <>
      <Base>
        <div className="container-fluid px-0">
          <Skills activeId={id} />
        </div>
      </Base>
    </>
  );
};

export default SkillsView;
